import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { useAuth } from "@/contexts/AuthContext";
import { useToast } from "@/hooks/use-toast";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Plus, Edit, Trash2, Send, Bell, Image, Users, User, FileEdit, Check, Loader2, Megaphone, Gift, Star, Heart, AlertCircle, Info, PartyPopper, Sparkles, Trophy, LucideIcon, Eye, ExternalLink } from "lucide-react";
import { format } from "date-fns";

const ICON_OPTIONS = ["Bell", "Megaphone", "Gift", "Star", "Heart", "AlertCircle", "Info", "PartyPopper", "Sparkles", "Trophy"];

// Icon component map for dynamic rendering
const ICON_MAP: Record<string, LucideIcon> = {
  Bell,
  Megaphone,
  Gift,
  Star,
  Heart,
  AlertCircle,
  Info,
  PartyPopper,
  Sparkles,
  Trophy
};

const DynamicIcon = ({ name, className }: { name: string; className?: string }) => {
  const IconComponent = ICON_MAP[name] || Bell;
  return <IconComponent className={className} />;
};

interface NotificationTemplate {
  id: string;
  title_zh: string;
  title_en: string;
  title_pt: string;
  content_zh: string;
  content_en: string;
  content_pt: string;
  icon: string;
  image_url: string | null;
  link: string | null;
  status: string;
  created_at: string;
  updated_at: string;
}

interface SendRecord {
  id: string;
  template_id: string | null;
  title: string;
  content: string;
  target_type: string;
  target_role: string | null;
  target_user_ids: string[] | null;
  sent_count: number;
  sent_at: string;
}

export default function AdminNotifications() {
  const { t, i18n } = useTranslation();
  const { user } = useAuth();
  const { toast } = useToast();
  const queryClient = useQueryClient();

  const [isTemplateDialogOpen, setIsTemplateDialogOpen] = useState(false);
  const [isSendDialogOpen, setIsSendDialogOpen] = useState(false);
  const [isPreviewDialogOpen, setIsPreviewDialogOpen] = useState(false);
  const [editingTemplate, setEditingTemplate] = useState<NotificationTemplate | null>(null);
  const [selectedTemplate, setSelectedTemplate] = useState<NotificationTemplate | null>(null);
  const [previewTemplate, setPreviewTemplate] = useState<NotificationTemplate | null>(null);
  const [previewLanguage, setPreviewLanguage] = useState<"zh" | "en" | "pt">("zh");
  const [activeTab, setActiveTab] = useState("templates");

  // Form state
  const [formData, setFormData] = useState({
    title_zh: "",
    title_en: "",
    title_pt: "",
    content_zh: "",
    content_en: "",
    content_pt: "",
    icon: "Bell",
    image_url: "",
    link: "",
    status: "draft"
  });

  // Send form state
  const [sendData, setSendData] = useState({
    targetType: "all" as "all" | "role" | "specific",
    targetRole: "" as string,
    targetUserIds: [] as string[],
    userSearch: ""
  });

  // Fetch templates
  const { data: templates = [], isLoading: templatesLoading } = useQuery({
    queryKey: ["notification-templates"],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("notification_templates")
        .select("*")
        .order("created_at", { ascending: false });
      if (error) throw error;
      return data as NotificationTemplate[];
    }
  });

  // Fetch send records
  const { data: sendRecords = [], isLoading: recordsLoading } = useQuery({
    queryKey: ["notification-send-records"],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("notification_send_records")
        .select("*")
        .order("sent_at", { ascending: false });
      if (error) throw error;
      return data as SendRecord[];
    }
  });

  // Fetch users for specific targeting
  const { data: users = [] } = useQuery({
    queryKey: ["all-users-for-notification"],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("profiles")
        .select("id, display_name")
        .order("display_name");
      if (error) throw error;
      return data;
    }
  });

  // Create/Update template mutation
  const templateMutation = useMutation({
    mutationFn: async (data: typeof formData & { id?: string }) => {
      if (data.id) {
        const { error } = await supabase
          .from("notification_templates")
          .update({
            title_zh: data.title_zh,
            title_en: data.title_en,
            title_pt: data.title_pt,
            content_zh: data.content_zh,
            content_en: data.content_en,
            content_pt: data.content_pt,
            icon: data.icon,
            image_url: data.image_url || null,
            link: data.link || null,
            status: data.status
          })
          .eq("id", data.id);
        if (error) throw error;
      } else {
        const { error } = await supabase
          .from("notification_templates")
          .insert({
            title_zh: data.title_zh,
            title_en: data.title_en,
            title_pt: data.title_pt,
            content_zh: data.content_zh,
            content_en: data.content_en,
            content_pt: data.content_pt,
            icon: data.icon,
            image_url: data.image_url || null,
            link: data.link || null,
            status: data.status,
            created_by: user?.id
          });
        if (error) throw error;
      }
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["notification-templates"] });
      setIsTemplateDialogOpen(false);
      resetForm();
      toast({ title: t("common.success"), description: editingTemplate ? t("common.updated") : t("common.created") });
    },
    onError: (error) => {
      toast({ title: t("common.error"), description: error.message, variant: "destructive" });
    }
  });

  // Delete template mutation
  const deleteMutation = useMutation({
    mutationFn: async (id: string) => {
      const { error } = await supabase
        .from("notification_templates")
        .delete()
        .eq("id", id);
      if (error) throw error;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["notification-templates"] });
      toast({ title: t("common.success"), description: t("common.deleted") });
    }
  });

  // Send notification mutation
  const sendMutation = useMutation({
    mutationFn: async () => {
      if (!selectedTemplate) throw new Error("No template selected");

      // Get target user IDs with pagination
      let targetUserIds: string[] = [];
      
      const fetchAllProfileIds = async () => {
        const allIds: string[] = [];
        let page = 0;
        const pageSize = 1000;
        
        while (true) {
          const { data, error } = await supabase
            .from("profiles")
            .select("id")
            .range(page * pageSize, (page + 1) * pageSize - 1);
          if (error) throw error;
          
          if (data && data.length > 0) {
            allIds.push(...data.map(item => item.id));
            if (data.length < pageSize) break;
            page++;
          } else {
            break;
          }
        }
        return allIds;
      };
      
      const fetchAllRoleUserIds = async (role: "user" | "merchant" | "supervisor" | "admin") => {
        const allIds: string[] = [];
        let page = 0;
        const pageSize = 1000;
        
        while (true) {
          const { data, error } = await supabase
            .from("user_roles")
            .select("user_id")
            .eq("role", role)
            .range(page * pageSize, (page + 1) * pageSize - 1);
          if (error) throw error;
          
          if (data && data.length > 0) {
            allIds.push(...data.map(item => item.user_id));
            if (data.length < pageSize) break;
            page++;
          } else {
            break;
          }
        }
        return allIds;
      };
      
      if (sendData.targetType === "all") {
        targetUserIds = await fetchAllProfileIds();
      } else if (sendData.targetType === "role") {
        const roleValue = sendData.targetRole as "user" | "merchant" | "supervisor" | "admin";
        targetUserIds = await fetchAllRoleUserIds(roleValue);
      } else {
        targetUserIds = sendData.targetUserIds;
      }

      if (targetUserIds.length === 0) {
        throw new Error("No users to send to");
      }

      // Create notifications for each user
      const notifications = targetUserIds.map(userId => ({
        user_id: userId,
        type: "activity_update" as const, // Using existing notification type
        title: getLocalizedContent(selectedTemplate, "title"),
        content: getLocalizedContent(selectedTemplate, "content"),
        link: selectedTemplate.link
      }));

      const { error: notifError } = await supabase
        .from("notifications")
        .insert(notifications);
      if (notifError) throw notifError;

      // Record the send
      const targetRoleValue = sendData.targetType === "role" 
        ? sendData.targetRole as "user" | "merchant" | "supervisor" | "admin" 
        : null;
      
      const { error: recordError } = await supabase
        .from("notification_send_records")
        .insert({
          template_id: selectedTemplate.id,
          title: getLocalizedContent(selectedTemplate, "title"),
          content: getLocalizedContent(selectedTemplate, "content"),
          target_type: sendData.targetType,
          target_role: targetRoleValue,
          target_user_ids: sendData.targetType === "specific" ? sendData.targetUserIds : null,
          sent_count: targetUserIds.length,
          sent_by: user?.id
        });
      if (recordError) throw recordError;

      return targetUserIds.length;
    },
    onSuccess: (count) => {
      queryClient.invalidateQueries({ queryKey: ["notification-send-records"] });
      setIsSendDialogOpen(false);
      setSelectedTemplate(null);
      setSendData({ targetType: "all", targetRole: "", targetUserIds: [], userSearch: "" });
      toast({ 
        title: t("common.success"), 
        description: t("admin.notificationsSent", { count }) || `已发送 ${count} 条通知`
      });
    },
    onError: (error) => {
      toast({ title: t("common.error"), description: error.message, variant: "destructive" });
    }
  });

  const getLocalizedContent = (template: NotificationTemplate, field: "title" | "content") => {
    const lang = i18n.language;
    if (field === "title") {
      return lang === "zh" ? template.title_zh : lang === "pt" ? template.title_pt : template.title_en;
    }
    return lang === "zh" ? template.content_zh : lang === "pt" ? template.content_pt : template.content_en;
  };

  const resetForm = () => {
    setFormData({
      title_zh: "",
      title_en: "",
      title_pt: "",
      content_zh: "",
      content_en: "",
      content_pt: "",
      icon: "Bell",
      image_url: "",
      link: "",
      status: "draft"
    });
    setEditingTemplate(null);
  };

  const openEditDialog = (template: NotificationTemplate) => {
    setEditingTemplate(template);
    setFormData({
      title_zh: template.title_zh,
      title_en: template.title_en,
      title_pt: template.title_pt,
      content_zh: template.content_zh,
      content_en: template.content_en,
      content_pt: template.content_pt,
      icon: template.icon || "Bell",
      image_url: template.image_url || "",
      link: template.link || "",
      status: template.status
    });
    setIsTemplateDialogOpen(true);
  };

  const openSendDialog = (template: NotificationTemplate) => {
    setSelectedTemplate(template);
    setIsSendDialogOpen(true);
  };

  const openPreviewDialog = (template: NotificationTemplate) => {
    setPreviewTemplate(template);
    setPreviewLanguage(i18n.language as "zh" | "en" | "pt");
    setIsPreviewDialogOpen(true);
  };

  const getPreviewContent = (template: NotificationTemplate, lang: "zh" | "en" | "pt") => {
    return {
      title: lang === "zh" ? template.title_zh : lang === "pt" ? template.title_pt : template.title_en,
      content: lang === "zh" ? template.content_zh : lang === "pt" ? template.content_pt : template.content_en
    };
  };

  const filteredUsers = users.filter(u => 
    u.display_name?.toLowerCase().includes(sendData.userSearch.toLowerCase())
  );

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold flex items-center gap-2">
            <Bell className="h-6 w-6" />
            {t("admin.notifications", "发送通知")}
          </h1>
          <p className="text-muted-foreground">{t("admin.notificationsDesc", "管理通知模板和发送站内通知")}</p>
        </div>
      </div>

      <Tabs value={activeTab} onValueChange={setActiveTab}>
        <TabsList>
          <TabsTrigger value="templates">{t("admin.templates", "模板管理")}</TabsTrigger>
          <TabsTrigger value="records">{t("admin.sendRecords", "发送记录")}</TabsTrigger>
        </TabsList>

        <TabsContent value="templates" className="space-y-4">
          <div className="flex justify-end">
            <Dialog open={isTemplateDialogOpen} onOpenChange={(open) => {
              setIsTemplateDialogOpen(open);
              if (!open) resetForm();
            }}>
              <DialogTrigger asChild>
                <Button>
                  <Plus className="h-4 w-4 mr-2" />
                  {t("admin.createTemplate", "创建模板")}
                </Button>
              </DialogTrigger>
              <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
                <DialogHeader>
                  <DialogTitle>
                    {editingTemplate ? t("admin.editTemplate", "编辑模板") : t("admin.createTemplate", "创建模板")}
                  </DialogTitle>
                </DialogHeader>

                <Tabs defaultValue="zh" className="w-full">
                  <TabsList className="grid w-full grid-cols-3">
                    <TabsTrigger value="zh">中文</TabsTrigger>
                    <TabsTrigger value="en">English</TabsTrigger>
                    <TabsTrigger value="pt">Português</TabsTrigger>
                  </TabsList>

                  <TabsContent value="zh" className="space-y-4">
                    <div>
                      <Label>{t("common.title")}</Label>
                      <Input
                        value={formData.title_zh}
                        onChange={(e) => setFormData({ ...formData, title_zh: e.target.value })}
                        placeholder="通知标题"
                      />
                    </div>
                    <div>
                      <Label>{t("common.content")}</Label>
                      <Textarea
                        value={formData.content_zh}
                        onChange={(e) => setFormData({ ...formData, content_zh: e.target.value })}
                        placeholder="通知内容"
                        rows={4}
                      />
                    </div>
                  </TabsContent>

                  <TabsContent value="en" className="space-y-4">
                    <div>
                      <Label>{t("common.title")}</Label>
                      <Input
                        value={formData.title_en}
                        onChange={(e) => setFormData({ ...formData, title_en: e.target.value })}
                        placeholder="Notification Title"
                      />
                    </div>
                    <div>
                      <Label>{t("common.content")}</Label>
                      <Textarea
                        value={formData.content_en}
                        onChange={(e) => setFormData({ ...formData, content_en: e.target.value })}
                        placeholder="Notification Content"
                        rows={4}
                      />
                    </div>
                  </TabsContent>

                  <TabsContent value="pt" className="space-y-4">
                    <div>
                      <Label>{t("common.title")}</Label>
                      <Input
                        value={formData.title_pt}
                        onChange={(e) => setFormData({ ...formData, title_pt: e.target.value })}
                        placeholder="Título da Notificação"
                      />
                    </div>
                    <div>
                      <Label>{t("common.content")}</Label>
                      <Textarea
                        value={formData.content_pt}
                        onChange={(e) => setFormData({ ...formData, content_pt: e.target.value })}
                        placeholder="Conteúdo da Notificação"
                        rows={4}
                      />
                    </div>
                  </TabsContent>
                </Tabs>

                <div className="grid grid-cols-2 gap-4 mt-4">
                  <div>
                    <Label>{t("admin.icon", "图标")}</Label>
                    <Select value={formData.icon} onValueChange={(v) => setFormData({ ...formData, icon: v })}>
                      <SelectTrigger>
                        <div className="flex items-center gap-2">
                          <DynamicIcon name={formData.icon} className="h-4 w-4" />
                          <span>{formData.icon}</span>
                        </div>
                      </SelectTrigger>
                      <SelectContent>
                        {ICON_OPTIONS.map((icon) => (
                          <SelectItem key={icon} value={icon}>
                            <div className="flex items-center gap-2">
                              <DynamicIcon name={icon} className="h-4 w-4" />
                              <span>{icon}</span>
                            </div>
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                  <div>
                    <Label>{t("common.status")}</Label>
                    <Select value={formData.status} onValueChange={(v) => setFormData({ ...formData, status: v })}>
                      <SelectTrigger>
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="draft">{t("common.draft", "草稿")}</SelectItem>
                        <SelectItem value="published">{t("common.published", "已发布")}</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                </div>

                <div>
                  <Label>{t("admin.imageUrl", "图片URL（可选）")}</Label>
                  <Input
                    value={formData.image_url}
                    onChange={(e) => setFormData({ ...formData, image_url: e.target.value })}
                    placeholder="https://..."
                  />
                </div>

                <div>
                  <Label>{t("admin.link", "链接（可选）")}</Label>
                  <Input
                    value={formData.link}
                    onChange={(e) => setFormData({ ...formData, link: e.target.value })}
                    placeholder="/activity/xxx"
                  />
                </div>

                <DialogFooter>
                  <Button variant="outline" onClick={() => setIsTemplateDialogOpen(false)}>
                    {t("common.cancel")}
                  </Button>
                  <Button 
                    onClick={() => templateMutation.mutate({ ...formData, id: editingTemplate?.id })}
                    disabled={templateMutation.isPending || !formData.title_zh || !formData.content_zh}
                  >
                    {templateMutation.isPending && <Loader2 className="h-4 w-4 mr-2 animate-spin" />}
                    {editingTemplate ? t("common.save") : t("common.create")}
                  </Button>
                </DialogFooter>
              </DialogContent>
            </Dialog>
          </div>

          {templatesLoading ? (
            <div className="flex justify-center py-8">
              <Loader2 className="h-8 w-8 animate-spin" />
            </div>
          ) : (
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
              {templates.map((template) => (
                <Card key={template.id}>
                  <CardHeader className="pb-2">
                    <div className="flex items-start justify-between">
                      <CardTitle className="text-lg">{getLocalizedContent(template, "title")}</CardTitle>
                      <Badge variant={template.status === "published" ? "default" : "secondary"}>
                        {template.status === "published" ? (
                          <><Check className="h-3 w-3 mr-1" />{t("common.published", "已发布")}</>
                        ) : (
                          <><FileEdit className="h-3 w-3 mr-1" />{t("common.draft", "草稿")}</>
                        )}
                      </Badge>
                    </div>
                    <CardDescription className="line-clamp-2">
                      {getLocalizedContent(template, "content")}
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    <div className="flex items-center gap-2 text-sm text-muted-foreground mb-4">
                      <DynamicIcon name={template.icon || "Bell"} className="h-4 w-4" />
                      <span>{template.icon || "Bell"}</span>
                      {template.image_url && <Image className="h-4 w-4 ml-2" />}
                    </div>
                    <div className="flex gap-2 flex-wrap">
                      <Button size="sm" variant="outline" onClick={() => openPreviewDialog(template)}>
                        <Eye className="h-4 w-4 mr-1" />
                        {t("common.preview", "预览")}
                      </Button>
                      <Button size="sm" variant="outline" onClick={() => openEditDialog(template)}>
                        <Edit className="h-4 w-4 mr-1" />
                        {t("common.edit")}
                      </Button>
                      <Button 
                        size="sm" 
                        variant="outline"
                        onClick={() => deleteMutation.mutate(template.id)}
                        disabled={deleteMutation.isPending}
                      >
                        <Trash2 className="h-4 w-4 mr-1" />
                        {t("common.delete")}
                      </Button>
                      {template.status === "published" && (
                        <Button size="sm" onClick={() => openSendDialog(template)}>
                          <Send className="h-4 w-4 mr-1" />
                          {t("common.send", "发送")}
                        </Button>
                      )}
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          )}
        </TabsContent>

        <TabsContent value="records">
          <Card>
            <CardHeader>
              <CardTitle>{t("admin.sendRecords", "发送记录")}</CardTitle>
            </CardHeader>
            <CardContent>
              {recordsLoading ? (
                <div className="flex justify-center py-8">
                  <Loader2 className="h-8 w-8 animate-spin" />
                </div>
              ) : (
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>{t("common.title")}</TableHead>
                      <TableHead>{t("admin.targetType", "发送对象")}</TableHead>
                      <TableHead>{t("admin.sentCount", "发送数量")}</TableHead>
                      <TableHead>{t("admin.sentAt", "发送时间")}</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {sendRecords.map((record) => (
                      <TableRow key={record.id}>
                        <TableCell className="font-medium">{record.title}</TableCell>
                        <TableCell>
                          <Badge variant="outline">
                            {record.target_type === "all" ? (
                              <><Users className="h-3 w-3 mr-1" />{t("admin.allUsers", "全部用户")}</>
                            ) : record.target_type === "role" ? (
                              <><User className="h-3 w-3 mr-1" />{record.target_role}</>
                            ) : (
                              <><User className="h-3 w-3 mr-1" />{t("admin.specificUsers", "指定用户")}</>
                            )}
                          </Badge>
                        </TableCell>
                        <TableCell>{record.sent_count}</TableCell>
                        <TableCell>{format(new Date(record.sent_at), "yyyy-MM-dd HH:mm")}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              )}
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Send Dialog */}
      <Dialog open={isSendDialogOpen} onOpenChange={setIsSendDialogOpen}>
        <DialogContent className="max-w-md">
          <DialogHeader>
            <DialogTitle>{t("admin.sendNotification", "发送通知")}</DialogTitle>
            <DialogDescription>
              {selectedTemplate && getLocalizedContent(selectedTemplate, "title")}
            </DialogDescription>
          </DialogHeader>

          <div className="space-y-4">
            <div>
              <Label>{t("admin.targetType", "发送对象")}</Label>
              <Select 
                value={sendData.targetType} 
                onValueChange={(v: "all" | "role" | "specific") => setSendData({ ...sendData, targetType: v })}
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">{t("admin.allUsers", "全部用户")}</SelectItem>
                  <SelectItem value="role">{t("admin.byRole", "按身份")}</SelectItem>
                  <SelectItem value="specific">{t("admin.specificUsers", "指定用户")}</SelectItem>
                </SelectContent>
              </Select>
            </div>

            {sendData.targetType === "role" && (
              <div>
                <Label>{t("admin.selectRole", "选择身份")}</Label>
                <Select value={sendData.targetRole} onValueChange={(v) => setSendData({ ...sendData, targetRole: v })}>
                  <SelectTrigger>
                    <SelectValue placeholder={t("admin.selectRole")} />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="user">{t("common.user", "用户")}</SelectItem>
                    <SelectItem value="merchant">{t("common.merchant", "商家")}</SelectItem>
                    <SelectItem value="supervisor">{t("common.supervisor", "主管")}</SelectItem>
                    <SelectItem value="admin">{t("common.admin", "管理员")}</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            )}

            {sendData.targetType === "specific" && (
              <div className="space-y-2">
                <Label>{t("admin.selectUsers", "选择用户")}</Label>
                <Input
                  placeholder={t("admin.searchUsers", "搜索用户...")}
                  value={sendData.userSearch}
                  onChange={(e) => setSendData({ ...sendData, userSearch: e.target.value })}
                />
                <div className="max-h-40 overflow-y-auto border rounded-md p-2 space-y-1">
                  {filteredUsers.slice(0, 20).map((u) => (
                    <label key={u.id} className="flex items-center gap-2 p-1 hover:bg-muted rounded cursor-pointer">
                      <input
                        type="checkbox"
                        checked={sendData.targetUserIds.includes(u.id)}
                        onChange={(e) => {
                          if (e.target.checked) {
                            setSendData({ ...sendData, targetUserIds: [...sendData.targetUserIds, u.id] });
                          } else {
                            setSendData({ ...sendData, targetUserIds: sendData.targetUserIds.filter(id => id !== u.id) });
                          }
                        }}
                      />
                      <span className="text-sm">{u.display_name}</span>
                    </label>
                  ))}
                </div>
                {sendData.targetUserIds.length > 0 && (
                  <p className="text-sm text-muted-foreground">
                    {t("admin.selectedUsersCount", { count: sendData.targetUserIds.length })}
                  </p>
                )}
              </div>
            )}
          </div>

          <DialogFooter>
            <Button variant="outline" onClick={() => setIsSendDialogOpen(false)}>
              {t("common.cancel")}
            </Button>
            <Button 
              onClick={() => sendMutation.mutate()}
              disabled={sendMutation.isPending || (sendData.targetType === "role" && !sendData.targetRole) || (sendData.targetType === "specific" && sendData.targetUserIds.length === 0)}
            >
              {sendMutation.isPending && <Loader2 className="h-4 w-4 mr-2 animate-spin" />}
              <Send className="h-4 w-4 mr-2" />
              {t("common.send", "发送")}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      {/* Preview Dialog */}
      <Dialog open={isPreviewDialogOpen} onOpenChange={setIsPreviewDialogOpen}>
        <DialogContent className="max-w-lg">
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2">
              <Eye className="h-5 w-5" />
              {t("admin.previewNotification", "预览通知")}
            </DialogTitle>
            <DialogDescription>
              {t("admin.previewNotificationDesc", "查看不同语言版本的通知内容")}
            </DialogDescription>
          </DialogHeader>

          {previewTemplate && (
            <div className="space-y-4">
              {/* Language Switcher */}
              <Tabs value={previewLanguage} onValueChange={(v) => setPreviewLanguage(v as "zh" | "en" | "pt")}>
                <TabsList className="grid w-full grid-cols-3">
                  <TabsTrigger value="zh">中文</TabsTrigger>
                  <TabsTrigger value="en">English</TabsTrigger>
                  <TabsTrigger value="pt">Português</TabsTrigger>
                </TabsList>
              </Tabs>

              {/* Preview Card */}
              <Card className="border-2">
                <CardContent className="pt-4">
                  <div className="flex items-start gap-3">
                    {/* Icon */}
                    <div className="flex-shrink-0 w-10 h-10 rounded-full bg-primary/10 flex items-center justify-center">
                      <DynamicIcon name={previewTemplate.icon || "Bell"} className="h-5 w-5 text-primary" />
                    </div>
                    
                    {/* Content */}
                    <div className="flex-1 space-y-2">
                      <h4 className="font-semibold text-base">
                        {getPreviewContent(previewTemplate, previewLanguage).title}
                      </h4>
                      <p className="text-sm text-muted-foreground whitespace-pre-wrap">
                        {getPreviewContent(previewTemplate, previewLanguage).content}
                      </p>
                      
                      {/* Image Preview */}
                      {previewTemplate.image_url && (
                        <div className="mt-3">
                          <img 
                            src={previewTemplate.image_url} 
                            alt="Notification" 
                            className="rounded-md max-h-40 object-cover"
                          />
                        </div>
                      )}
                      
                      {/* Link Preview */}
                      {previewTemplate.link && (
                        <div className="mt-2 flex items-center gap-1 text-sm text-primary">
                          <ExternalLink className="h-3 w-3" />
                          <span className="truncate">{previewTemplate.link}</span>
                        </div>
                      )}
                    </div>
                  </div>
                </CardContent>
              </Card>

              {/* Meta Info */}
              <div className="text-xs text-muted-foreground space-y-1">
                <div className="flex items-center gap-2">
                  <span>{t("common.status")}:</span>
                  <Badge variant={previewTemplate.status === "published" ? "default" : "secondary"} className="text-xs">
                    {previewTemplate.status === "published" ? t("common.published", "已发布") : t("common.draft", "草稿")}
                  </Badge>
                </div>
                <div>
                  {t("common.createdAt")}: {format(new Date(previewTemplate.created_at), "yyyy-MM-dd HH:mm")}
                </div>
              </div>
            </div>
          )}

          <DialogFooter>
            <Button variant="outline" onClick={() => setIsPreviewDialogOpen(false)}>
              {t("common.close", "关闭")}
            </Button>
            {previewTemplate?.status === "published" && (
              <Button onClick={() => {
                setIsPreviewDialogOpen(false);
                openSendDialog(previewTemplate);
              }}>
                <Send className="h-4 w-4 mr-2" />
                {t("common.send", "发送")}
              </Button>
            )}
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  );
}
